.main{
    height: 130vh;
    font-family: 'Roboto Slab', serif;
}

.title{
    margin-top: 3.5%;
    height: 50px;
    text-align: center;
}

.img{
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.title>span{
    font-size: 40px;
    font-weight: bold;
    letter-spacing: 5px;
}

.projectContainer{
    display: flex;
    flex-wrap: wrap;
}

.projectCard{
    width: 20%;
    height: 380px;
    border: 1px solid #f5f8fa;
    margin: auto;
    margin-top: 4%;
    border-radius: 5%;
    background: white;
    box-shadow:  18px 18px 19px #b5b8b9,
                -18px -18px 19px #ffffff;
}

.projectCardPic{
    width: 95%;
    height: 160px;
    margin: auto;
    margin-top: 2%;
    border-radius: 5%;
}

.projectCardName{
    width: 95%;
    height: 30px;
    margin: auto;
    margin-top: 1%;
}

.projectCardName>span{
    font-weight: bold;
}

.projectCardStack{
    display: flex;
    width: 95%;
    height: 30px;
    margin: auto;
    margin-top: 1%;
}

.projectCardDesc{
    width: 95%;
    height: 80px;
    margin: auto;
    margin-top: 1%;
}

.projectCardDesc>p{
    font-size: 13px;
}

.projectCardBtn{
    width: 95%;
    height: 30px;
    margin: auto;
    margin-top: 5%;
    display: flex;
}

.projectCardBtn>button{
    margin-right: 2%;
    cursor: pointer;
}

@media only screen and (min-width: 200px) and (max-width: 400px){
    .main{
        height: auto;
        margin-top: 18%;
    }

    .projectCard{
        width: 80%;
        height: 380px;
        border: 1px solid white;
        margin: auto;
        margin-top: 8%;
        background: white;
        box-shadow:  18px 18px 19px #b5b8b9,
                    -18px -18px 19px #ffffff;
    }

    .projectCardDesc>p{
        font-size: 12px;
    }
}

@media only screen and (min-width: 400px) and (max-width: 700px){
    .main{
        height: auto;
    }

    .projectCard{
        width: 48%;
        height: 430px;
        border: 1px solid white;
        margin: auto;
        margin-top: 4%;
        border-radius: 3%;
    }
    
    .projectCardDesc>p{
        font-size: 12px;
    }
    .projectCardStack{
        display: flex;
        width: 95%;
        height: 30px;
        margin: auto;
        margin-top: 15%;
    }
    .projectCardBtn{
        width: 95%;
        height: 30px;
        margin: auto;
        margin-top: 5%;
        display: flex;
        flex-wrap: wrap;
    }
    
    .projectCardBtn>button{
        margin-right: 2%;
        cursor: pointer;
        height: 30px;
        margin-top: 2%;
    }
    
}

@media only screen and (min-width: 700px) and (max-width: 1024px){
    .main{
        height: auto;
    }

    .projectCard{
        width: 30%;
        height: 400px;
        border: 1px solid white;
        margin: auto;
        margin-top: 4%;
        border-radius: 3%;
    }
    .projectCardStack{
        display: flex;
        width: 95%;
        height: 30px;
        margin: auto;
        margin-top: 15%;
    }
}

@media only screen and (min-width: 1024px) and (max-width: 1440px){
    .main{
        height: auto;
    }

    .projectCard{
        width: 30%;
        height: 390px;
        border: 1px solid white;
        margin: auto;
        margin-top: 4%;
        border-radius: 5%;
    }
    .projectCardStack{
        display: flex;
        width: 95%;
        height: 30px;
        margin: auto;
        margin-top: 1%;
    }
}